import { Navigate } from 'react-router-dom';
import { useState, useEffect } from 'react';

function RequireAuthentication(WrappedComponent) {
  // eslint-disable-next-line
  return function (props) {
    const [isLoggedIn, setLoginStatus] = useState(null);
    const [loading, setLoading] = useState(null);

    useEffect(async () => {
      const newUrl = 'https://auth-stage.mymemorystore.in/oauth2/authinfo';
      const options = { method: 'GET', credentials: 'include' };
      setLoading(true);
      await fetch(newUrl, options).then((response) => setLoginStatus(response.ok));
      setLoading(false);
      return () => {
        setLoginStatus(null);
        setLoading(null);
      };
    }, []);

    return isLoggedIn && !loading ? (
      // eslint-disable-next-line
      <WrappedComponent {...props} />
    ) : (<Navigate to="/" replace />);
  };
}

export default RequireAuthentication;
