function Logout() {
  const belowmd = useMediaQuery((theme) => theme.breakpoints.down('md'));
  const textVariant = belowmd ? 'body1' : 'h6';
  return (
    <Button
      sx={{
        color: 'button.main',
        backgroundColor: 'button.main',
        marginX: 1,
        borderRadius: 3,
        paddingX: 2,
      }}
    >
      <a href="https://auth-stage.mymemorystore.in/oauth2/logout" target="_self" rel="noreferrer" style={{ textDecoration: 'none', color: 'black' }}>
        <Typography variant={textVariant}>Logout</Typography>
      </a>
    </Button>
  );
}

